import { motion, useScroll } from "framer-motion";
import { useRef } from "react";
import app from '../assets/app.svg'

const Mobile = () => {

    const ref = useRef<HTMLDivElement>(null)
    
    const {scrollYProgress} = useScroll({
        target: ref,
        offset: ['0 1', '1 1'],
    });
    
    return (
        <motion.div ref={ref} style={{
            display: 'flex', 
            marginTop: '30vh', 
            flexDirection: 'column', 
            marginBottom: 400,
            scale: scrollYProgress,
            opacity: scrollYProgress}}>
                <div className="descriptionAndImg">
                    <h2>Mobiln aplikace.</h2>
                    <div className="description">
                        <p className="servicesP">Modern mobiln aplikace pro modern podnikn. Pinme nejen kd, ale i kreativitu, abychom vytvoili mobiln aplikaci, kter otevr nov monosti pro Vai znaku.</p>
                    </div>

                    <img src={app} className='imgServices' alt='vvoj mobilnch aplikac na mru'/>
                </div>
        </motion.div>
    );
};

export default Mobile;